import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';
import Toolbar from '@material-ui/core/Toolbar';

const styles = theme => ({
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
});

class Orders extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      cards: [],
    }

    this.getOrders = this.getOrders.bind(this);
  }

  componentDidMount() {
    this.getOrders(0,20)
    this.timer = setInterval(()=> this.getOrders(0,20), 15000);
  }

  componentWillUnmount() {
    clearInterval(this.timer)
    this.timer = null;
  }

  getOrders = (offset, limit) => {
    fetch('https://api.opensea.io/wyvern/v1/orders?asset_contract_address=0x0e3a2a1f2146d86a604adc220b4967a898d7fe07&limit=50')
    .then(res => {
      return res.json()
    }).then(data => {
      let cards = data.orders.map((card) => {
        // console.log(card)
        let asset = card.asset
        return(asset ? { name: asset.name, tokenId: asset.token_id, thumbnail: asset.image_thumbnail_url, price: card.current_price/1000000000000000000, time: card.created_date } : {})
      })
      this.setState({cards: cards})
    })
  }

  render() {
    const { classes } = this.props;
    let currentTime = new Date();

    return (
      <React.Fragment>
        <Grid item xs={12}>
          <Paper className={classes.paper}>
            <Toolbar>
              <Typography variant="h6">
                Newly Listed
              </Typography>
            </Toolbar>
            <Table size={'small'}>
              <TableHead>
                <TableRow>
                  <TableCell></TableCell>
                  <TableCell>Name</TableCell>
                  <TableCell>Price (ETH)</TableCell>
                  <TableCell>Time</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {this.state.cards.map((card, index) => {
                  let cardTime = new Date(card.time);
                  cardTime.setHours(cardTime.getHours() - 8);

                  let diff = currentTime - cardTime;
                  let minutes = Math.floor(diff/60000);
                  let seconds = Math.floor(diff/1000);
                  return (
                    <TableRow hover key={index}>
                      <TableCell><Avatar variant="square" alt={card.name} src={card.thumbnail} /></TableCell>
                      <TableCell>{card.name}</TableCell>
                      <TableCell>{card.price}</TableCell>
                      <TableCell>{minutes > 0 ? minutes + ' minutes' : seconds + ' seconds'} ago</TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </Paper>
        </Grid>
      </React.Fragment>
    );
  }
}

Orders.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Orders);